import React, { Component } from 'react';
import {withStyles} from "@material-ui/styles"
import styles from "./Styles/MiniPaletteStyles"
import DeleteIcon from "@material-ui/icons/Delete"


class MiniPalette extends Component {
  constructor(props){
    super(props);
    this.deletePalette = this.deletePalette.bind(this);
  }


  deletePalette (e){
    e.stopPropagation();
    this.props.openDialog(this.props.id);
  }

  render(){
    const {classes, paletteName, emoji, colors, handleClick} = this.props;
    const miniBox = colors.map (color => (<div key={color.name} className= {classes.miniColor} style={{backgroundColor: color.color}}></div>))
    return (
      <div onClick={this.props.slideDown}>
      <div className={classes.root} onClick={handleClick}>
        <DeleteIcon className={classes.deleteIcon}  onClick={this.deletePalette} />
      <div className={classes.colors}>
        {miniBox}
      </div>
        <h5 className={classes.title}>{paletteName}<span className={classes.emoji}>{emoji}</span></h5>
      </div>
      </div>
    );
  }
}

export default withStyles(styles)(MiniPalette);